import { atob } from './pollify'
import renders from './renders'

/**
 * 
 * @param {object} file 
 */
export async function readBuffer(file) {
  return new Promise((resolve, reject) => {
    const reader = new FileReader()
    reader.onload = (loadEvent) => resolve(loadEvent.target.result)
    reader.onerror = (e) => reject(e)
    reader.readAsArrayBuffer(file)
  })
}

/**
 * base64
 * @param {object} buffer 
 */
export async function readDataURL(buffer) {
  return new Promise((resolve, reject) => {
    const reader = new FileReader()
    reader.onload = (loadEvent) => resolve(loadEvent.target.result)
    reader.onerror = (e) => reject(e)
    reader.readAsDataURL(new Blob([buffer]))
  })
}

/**
 * utf8
 * @param {object} buffer 
 */
export async function readText(buffer) {
  return new Promise((resolve, reject) => {
    const reader = new FileReader()
    reader.onload = (loadEvent) => resolve(loadEvent.target.result)
    reader.onerror = (e) => reject(e)
    reader.readAsText(new Blob([buffer]), 'utf-8')
  })
}

/**
 * 
 * @param {string} name 
 */
export function getExtend(name) {
  //  URL 
  const cleanName = name.split('?')[0].split('#')[0]
  const dot = cleanName.lastIndexOf('.')
  return dot === -1 ? '' : cleanName.substring(dot + 1)
}

/**
 * 
 * @param {string | blob} file url/blob/base64
 * @param {string} name abc.jpgurl
 */
export function fileDownload(file, name) {
  if (!file) {
    throw new Error('')
  }

  // fileurl
  if (typeof file === 'string' && file.indexOf('http') > -1) {
    name = name ? name : getUrlFileName(file)
    const link = document.createElement('a')
    link.style.display = 'none'
    link.href = file
    link.target = '_blank'
    link.setAttribute('download', name) // exemple.txt
    document.body.appendChild(link)
    link.click()
    return
  }

  if (!name) {
    throw new Error('')
  }

  // filebase64blob
  if (typeof file === 'string') {
    file = base64toBlob(file)
  }

  // fileblob
  if (window.navigator.msSaveBlob) {
    window.navigator.msSaveOrOpenBlob(file, name)
  } else {
    const url = window.URL.createObjectURL(new Blob([file]))
    const link = document.createElement('a')
    link.style.display = 'none'
    link.href = url
    link.target = '_blank'
    link.setAttribute('download', name) // exemple.txt
    document.body.appendChild(link)
    link.click()
  }
}

/**
 * Base64  Blob
 * @param {string} base64String Blob
 */
export function base64toBlob(base64String) {
  var arr = base64String.split(','),
    mime = arr[0].match(/:(.*?);/)[1],
    bstr = atob(arr[1]),
    n = bstr.length,
    u8arr = new Uint8Array(n)
  while (n--) {
    u8arr[n] = bstr.charCodeAt(n)
  }
  return new Blob([u8arr], {
    type: mime
  })
}

/**
 * +
 * @param {string} url 
 */
export function getUrlFileName(url) {
  if (!url) return ''
  const file = url.split('/')
  return file[file.length - 1] || ''
}

/**
 * 
 * @param {buffer} buffer 
 * @param {buffer} target 
 * @param {buffer} type 
 * @param {buffer} name 
 **/
export async function render(buffer, target, type, name) {
  const handler = renders[type]
  if (handler) {
    return handler(...arguments)
  }
  return renders.error(...arguments)
}

/**
 * Blob
 * @param {Blob} blob 
 **/
export function generateFilenameFromBlob(blob) {
  // MIME
  const mimes = {
    // 
    'application/vnd.openxmlformats-officedocument.wordprocessingml.document': 'docx',
    'application/vnd.openxmlformats-officedocument.presentationml.presentation': 'pptx',
    'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet': 'xlsx',
    'application/pdf': 'pdf',

    // 
    'image/gif': 'gif',
    'image/jpeg': 'jpg',
    'image/jpg': 'jpg',
    'image/png': 'png',
    'image/bmp': 'bmp',
    'image/tiff': 'tiff',
    'image/tif': 'tif',
    'image/svg+xml': 'svg',

    // 
    'text/plain': 'txt',
    'application/json': 'json',
    'text/javascript': 'js',
    'application/javascript': 'js',
    'text/css': 'css',
    'text/html': 'html',
    'text/markdown': 'md',
    'application/xml': 'xml',
    'text/xml': 'xml',
    'text/x-java-source': 'java',
    'text/x-python': 'py',
    'text/typescript': 'ts',
    'text/tsx': 'tsx',
    'text/jsx': 'jsx',
    'text/x-log': 'log',

    // 
    'video/mp4': 'mp4',
    'video/webm': 'webm',
    'video/ogg': 'ogv',

    // 
    'audio/mpeg': 'mp3',
    'audio/mp3': 'mp3',
    'audio/wav': 'wav',
    'audio/ogg': 'ogg',
  }

  //  bin 
  const ext = mimes[blob.type]

  if (!ext) {
    throw new Error('')
  }

  // YYYYMMDD_HHmmss
  const timestamp = new Date().toISOString()
    .replace(/[-:T]/g, '')    //  - : T
    .split('.')[0]            // 
    .replace('Z', '')

  // 
  return `${timestamp}.${ext}`
}
